body::before {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0; 
    background: #1c2020;
    z-index: 6;
    top: 50%;
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
        transform: translateY(-100%);
}
body::after {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0; 
    background: #1c2020;
    z-index: 6;
    bottom: 50%;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
        transform: translateY(100%);
}
.home-web {
    width: 100%;
    z-index: 5;
}
.home-web-wrapper {
    background: -webkit-radial-gradient(52% 93%, circle, #023365, #000000);
    background: -o-radial-gradient(52% 93%, circle, #023365, #000000);
    background: radial-gradient(circle at 52% 93%, #023365, #000000);
    width: 100%;
    height: 100%;
    min-height: 525px;
    min-width: 550px;
}
.home-wrapper {
    width: 100%;
    height: 100%;
}
.home-mobile {
    overflow-y: hidden;
    width: 100%;
    height: 100%;
}
.home-web {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0 ,-50%);
        -ms-transform: translate(0 ,-50%);
            transform: translate(0 ,-50%);
}
.err-msg {
    color: white;
    font-size: 17px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 100;
    direction: rtl;
    font-family: Heebo, Alef-Regular, Alef-Bold, sans-serif;
}



